<template>
    <div id="classification">
        <span style="color: #ff6161;">*</span>
        <span style="font-size: 15px; font-weight: bold">请选择合适的分类:</span>
        <el-cascader
            style="width: 400px;"
            :options="classifyList"
            :props="props_dict"
            filterable
            v-model="selectedOptions"
            @change="handleChange">
        </el-cascader>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                props_dict: {
                    value: 'catId',
                    label: 'catName',
                    children: 'children'
                },
                selectedOptions: this.allSortValueList,
            }
        },
        props: ['classifyList', 'allSortValueList'],
        methods: {
            handleChange(value) {
                this.$emit('getSortValue', this.selectedOptions)
            }
        },
        watch: {
            allSortValueList(value) {
                this.selectedOptions = value
            }
        }
    }
</script>

<style lang="scss" scoped>
    #classification {
        padding: 30px 0 57px 0;
    }
</style>



